* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: Arial, Helvetica, sans-serif;
  }
  
  /* ---------Global------------- */
  
  .container {
    margin: 0 auto;
  
  }
  
  /* ------------NavBAr---------- */
  header {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 40px 0;
    background-color: red;
  }
  
  .logo-pokedex {
    width: 150px;
    height: auto;
  }
  
  header form {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  
  .form-group input {
    display: flex;
    align-items: center;
    gap: 10px;
    border: 1px solid #555;
    padding: 10px 20px;
    border-radius: 25px;
    background-color: white;
    width: 300px;
    border: none;
    outline: none;
    font-size: 15px;
  }
  
  
  .btn-search {
    border: none;
    outline: none;
    border-radius: 25px;
    padding: 15px 30px;
    font-family: inherit;
    color: #fff;
    background-color: #006d77;
    cursor: pointer;
    font-weight: 500;
    letter-spacing: 2px;
  }
  
  .btn-search:hover {
    background-color: #83c5be;
    color: #000;
  
  }
  
  
  /* ---------Card List Pokemon--------- */
  .card-list-pokemon {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
    margin: 20px 0 0 30px;
  }
  
  .card-pokemon {
    cursor: pointer;
    text-decoration: none;
  }
  
  
  .card-img {
    background-color: #f2f2f2;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    height: 170px;
    width: 170px;
  }
  
  .card-img img {
    width: 100%;
    height: 100%;
  }
  
  .card-info {
    padding: 15px;
  }
  
  .card-info h3 {
    color: #333;
  }
  
  .pokemon-id {
    display: block;
    color: #888;
    margin-bottom: 15px;
  }
  
  .card-types {
    display: flex;
    gap: 10px;
    margin-top: 10px;
  }
  
  .card-types span {
    font-size: 12px;
    padding: 5px 20px;
    border-radius: 5px;
    color: #fff;
  }
  
  
  
  
  /* 
  ----------Pokemon Single Page--------
  */
  
  .main-pokemon {
    display: flex;
    flex-direction: column;
    width: 55%;
    height: 55%;
    margin-top: 50px;
    margin-bottom: 50px;
  }
  
  .header-main-pokemon {
    margin-top: 20px;
    display: flex;
    align-items: center;
    position: relative;
    gap: 30%;
  }
  
  
  .number-pokemon {
  
    font-size: 50px;
    font-weight: 500;
    color: #83c5be;
  }
  
  
  .container-img-pokemon img {
    height: auto;
    width: 200px;
  
  }
  
  
  
  .container-info-pokemon h1 {
    text-align: left;
    font-size: 50px;
    margin-bottom: 30px;
  
  }
  
  .info-pokemon {
    display: flex;
    justify-content: flex-start;
    gap: 20px;
    margin-top: 20px;
  }
  
  .group-info {
    display: flex;
    flex-direction: column;
    gap: 10px;
  
  }
  
  .group-info p {
    font-weight: 700px;
    font-size: 20px;
  }
  
  /* Stats */
  .container-stats {
    display: flex;
    gap: 20px;
    align-items: stretch;
    flex-direction: column;
  }
  
  .stats {
    display: flex;
    flex-direction: column;
    gap: 20px;
    flex: 1;
  }
  
  .stat-group {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  
  .stat-group span {
    flex: 20%;
    font-size: 18px;
  }
  
  .progress-bar {
    width: 40%;
    height: 30px;
    background-color: #006d77;
    border-radius: 10px;
  
  }
  
  /* ------------Search Page---------------- */
  
  .p-search {
    margin: 30px;
    font-size: 22px;
  }
  
  .p-search span {
    font-weight: 700;
  }
  
  /* -------------Loader------------- */
  
  .container-loader {
    display: flex;
    justify-content: center;
    margin: 200px 0;
  }
  
  /* ---------Btn next and previous page--------- */
  
  .container-btn-next-page {
    display: flex;
    justify-content: center;
  
  }
  
  .btn-next-page {
    margin: 20px;
    border: none;
    padding: 15px 40px;
    font-family: inherit;
    background-color: #006d77;
    color: #fff;
    border-radius: 50px;
    cursor: pointer;
    width: 150px;
  }
  
  .btn-next-page:hover {
    background-color: #83c5be;
    color: #000;
  }
  
  @media (max-width: 868px) {
    /* --------Styles for screens 868 pixels-------- */
  
    header {
      flex-direction: column;
      text-align: center;
    }
  
    .form-group input {
      max-width: none;
    }
  
    .card-list-pokemon {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 10px;
      margin: 10px;
    }
  
    .form-group input {
      display: flex;
      align-items: center;
      gap: 10px;
      border: 1px solid #555;
      padding: 10px 20px;
      border-radius: 25px;
      background-color: white;
      width: 200px;
      border: none;
      outline: none;
      font-size: 15px;
    }
  
    .header-main-pokemon {
      /* Reset flex-direction to row for larger viewports */
      flex-direction: column-reverse;
      align-items: flex-start;
      margin-bottom: 10px;
    }
  
  }
  
  
  
  /* ------------Types of Pokemon------------ */
  .water {
    background-color: #4592c4;
  }
  
  .grass {
    background-color: #9bcc50;
  }
  
  .poison {
    background-color: #7e0058;
  }
  
  .fire {
    background-color: #ff2002;
  }
  
  .bug {
    background-color: #729f3f;
  }
  
  .dragon {
    background: linear-gradient(180deg, #53a4cf 50%, #f16e57 50%);
  }
  
  .flying {
    background-color: #3dc7ef;
  }
  
  .ground {
    background-color: #ab9842;
  }
  
  .steel {
    background-color: #9eb7b8;
  }
  
  .psychic {
    background-color: #f366b9;
  }
  
  .ice {
    background-color: #51c4e7;
  }
  
  .ghost {
    background-color: #4d5b64;
  }
  
  .normal {
    background-color: #a4acaf;
  }
  
  .rock {
    background-color: #a38c21;
  }
  
  .electric {
    background-color: #bba909;
  }
  
  .fighting {
    background-color: #d56723;
  }
  
  .fairy {
    background-color: #fdb9e9;
  }
  
  .dark {
    background-color: #707070;
  }